import Button from '@material-ui/core/Button';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardHeader from '@material-ui/core/CardHeader';
import CardMedia from '@material-ui/core/CardMedia';
import Collapse from '@material-ui/core/Collapse';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/styles';
import PropTypes from 'prop-types';
import React, { useState } from 'react';

import styles from './ResearchPaper.css';

const ResearchPaper = ({ abstract, href, image, imageTitle, title }) => {
  const classes = makeStyles(styles)();
  const [abstractVisible, setAbstractVisible] = useState(false);

  return (
    <Card className={classes.paper}>
      <CardHeader
        title={title}
        titleTypographyProps={{
          className: classes.title,
          variant: `subtitle1`,
        }}
      />
      <CardMedia className={classes.media} image={image} title={imageTitle} />
      <Collapse in={abstractVisible}>
        <CardContent className={classes.cardContent}>
          <Typography
            className={classes.abstract}
            color="textSecondary"
            variant="body2">
            {abstract}
          </Typography>
        </CardContent>
      </Collapse>
      <CardActions className={classes.actions}>
        <Button
          className={classes.action}
          color="secondary"
          onClick={() => {
            setAbstractVisible((prev) => {
              return !prev;
            });
          }}>
          {abstractVisible ? `Hide` : `Read`} Abstract
        </Button>
        <Button
          className={classes.action}
          color="secondary"
          component="a"
          href={href}
          rel="noopener noreferrer"
          target="_blank">
          Read Full Paper
        </Button>
      </CardActions>
    </Card>
  );
};

ResearchPaper.propTypes = {
  abstract: PropTypes.string.isRequired,
  href: PropTypes.string.isRequired,
  image: PropTypes.string.isRequired,
  imageTitle: PropTypes.string.isRequired,
  title: PropTypes.string.isRequired,
};

export default ResearchPaper;
